import { CuratedSuggestedPrompt } from "@rp/react/common/hooks"
import { Box, Typography } from "@rp/react/web/mui/core"
import { SuggestedPromptGroup } from "./SuggestedPromptGroup"

type Props = {
  prompts: Array<CuratedSuggestedPrompt>
  onPromptClick: (prompt: CuratedSuggestedPrompt) => void
}
const SuggestedPromptsSearchResult = ({ prompts, onPromptClick }: Props) => {
  if (prompts.length === 0) {
    return <Box />
  }

  return (
    <Box>
      <Typography>
        There {prompts.length === 1 ? "is" : "are"}{" "}
        <Typography component="span" sx={{ fontWeight: 700 }}>
          {prompts.length ?? "0"} {prompts.length === 1 ? "result" : "results"}
        </Typography>{" "}
        matching your search
      </Typography>
      <SuggestedPromptGroup
        prompts={prompts}
        onPromptClick={onPromptClick}
        initialNumber={Infinity}
      />
    </Box>
  )
}
export { SuggestedPromptsSearchResult }
